import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from './../Services/auth.service';
import { ValidateService } from './../Services/validate.service';
import { ToastrService } from 'ngx-toastr';
@Component({
  selector: 'app-add-company',
  templateUrl: './add-company.component.html',
  styleUrls: ['./add-company.component.css']
})
export class AddCompanyComponent implements OnInit {
  name:String;
  address:String;
  email:String;
  fname:String;
  lname:String;
  phonenumber:String;
  employeeid:String;

  constructor(
    private validateService:ValidateService, 
    private toastr: ToastrService,
    private authService:AuthService,
    private router:Router
  ) { }

  ngOnInit() {
  }

  onRegisterSubmit() {
    const company = {
      name: this.name,
      address: this.address,
      email: this.email
    }

    // validate email

    if (!this.validateService.validateEmail(company.email)) {
      //console.log('Please use valid email');
      this.toastr.error('Please use valid email');
      return false;
    }

    //Register User
    this.authService.registerCompany(company).subscribe(data =>{
      if(data.success){
        //console.log("your now registerd");
        this.toastr.success('Company Added');
        this.router.navigate(['/dashboard']);
      }
      else{
        //console.log("Something went wrong");
        this.toastr.error(data.msg);
      }
    });
  }

}
